.notify {
  &__popup {
    background-color: @notify;
    color: @notify-inverted;
  }

  &__popup-inner {
    position: relative;
    .l-container-padding(@notify-padding-ver, @notify-padding-hoz);
  }

  &__icon {
    display: none;
  }

  &__title {
    margin-bottom: @notify-title-margin;
    .notify-title;

    // Converted media query to use min width to sync responsive trigger with js
    // https://github.com/adaptlearning/adapt_framework/issues/2865
    padding-inline-end: unit(@icon-size + @btn-margin + @btn-padding, rem);

    @media (min-width: @device-width-medium) {
      padding-inline-end: inherit;
    }
  }

  &__body {
    margin-bottom: @notify-body-margin;
  }

  &__instruction {
    margin-bottom: @notify-instruction-margin;
    .instruction-text;
  }

  &__body a,
  &__instruction a {
    .link-text;
    color: @notify-link;

    .no-touch &:hover {
      color: @notify-link-hover;
      .transition(color @duration ease-in);
    }
  }

  &__btn-container {
    margin-top: @btn-margin;
  }

  // Button
  &__btn {
    margin: @notify-btn-margin;
    padding: @notify-btn-padding-ver @notify-btn-padding-hoz;
  }

  & when (@notify-btn-color-style = filled) {
    &__btn {
      .notify-btn-color;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-btn-color-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-btn-color-focus;
      }
    }

    &__btn.is-disabled {
      .notify-btn-color-disabled;
    }
  }

  & when (@notify-btn-color-style = outline) {
    &__btn {
      .notify-btn-color-outline;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-btn-color-outline-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-btn-color-outline-focus;
      }
    }

    &__btn.is-disabled {
      .notify-btn-color-outline-disabled;
    }
  }

  // UI
  &__btn-icon {
    .ui-btn-ctrl;

    & when (@notify-btn-color-style = filled) {
      .notify-ui-btn-ctrl-color;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-ui-btn-ctrl-color-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-ui-btn-ctrl-color-focus;
      }
    }

    & when (@notify-btn-color-style = outline) {
      .notify-ui-btn-ctrl-color-outline;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-ui-btn-ctrl-color-outline-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-ui-btn-ctrl-color-outline-focus;
      }
    }
  }

  &__close-btn {
    position: absolute;
    top: 0;
    right: 0;

    .dir-rtl & {
      right: inherit;
      left: 0;
    }
  }

  // Notify prompt type icon
  // --------------------------------------------------
  // &__icon {
  //   position: absolute;
  //   top: 0;
  //   right: 0;

  //   .dir-rtl & {
  //     right: inherit;
  //     left: 0;
  //   }
  // }
}

.notify:not(.disable-animation) > .notify__popup {
  .transition(top var(--adapt-notify-duration) @animation-easing, transform var(--adapt-notify-duration) @animation-easing, opacity var(--adapt-notify-duration) @animation-easing, height var(--adapt-notify-duration) @animation-easing;);
  opacity: 0;
  transform: translateY(0%);

  .anim-show-after& {
    transform: translateY(-50%);
    opacity: 1;
  }

  .anim-hide-after& {
    transform: translateY(0%);
    opacity: 0;
  }
}

.notify:not(.disable-animation) > .notify__shadow {
  .transition(opacity var(--adapt-notify-duration) @animation-easing;);
  opacity: 0;

  .anim-show-after& {
    opacity: 1;
  }

  .anim-hide-after& {
    opacity: 0;
  }
}
