.notify-push {
  background-color: @notify;
  color: @notify-inverted;
  .transform(translateY(25px));
  .transition(opacity @duration ease-in-out, transform @duration ease-in-out;);

  &.is-active {
    .transform(translateY(0));
  }

  &__inner {
    padding-top: @item-padding;
    padding-bottom: @item-padding;
    padding-inline: @item-padding unit(@icon-size + @btn-margin + @btn-padding + @item-padding, rem);
  }

  &__title {
    margin-bottom: @notify-title-margin;
    .notify-title;
  }

  &__body a {
    .link-text;
    color: @notify-link;

    .no-touch &:hover {
      color: @notify-link-hover;
      .transition(color @duration ease-in);
    }
  }

  &__close-btn {
    position: absolute;
    top: 0;
    right: 0;
    .ui-btn-ctrl;

    & when (@notify-ui-btn-ctrl-color-style = filled) {
      .notify-ui-btn-ctrl-color;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-ui-btn-ctrl-color-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-ui-btn-ctrl-color-focus;
      }
    }

    & when (@notify-ui-btn-ctrl-color-style = outline) {
      .notify-ui-btn-ctrl-color-outline;

      .no-touch &:not(.is-disabled):not(.is-locked):hover {
        .notify-ui-btn-ctrl-color-outline-hover;
      }

      html:not(.a11y-disable-focusoutline) &:focus-visible {
        .notify-ui-btn-ctrl-color-outline-focus;
      }
    }

    .dir-rtl & {
      right: auto;
      left: 0;
    }
  }
}
